<template>
  <div class="row">
    <div class="col-md-7">
      <div class="col-md-12">
        
        <v-col cols="12" sm="12" md="80">
          <div class="overline">DATOS EXTRA ALUMNO</div>
          <hr />
          <v-form ref="form">
            <v-text-field outlined v-model="user.email" label="E-mail" required></v-text-field>
            <v-text-field outlined v-model="user.celular" label="Celular" required></v-text-field>
            <v-text-field outlined v-model="user.procedencia" label="Procedencia" required></v-text-field>
            <v-text-field outlined v-model="user.nivel" label="Nivel" required></v-text-field>
            <v-text-field outlined v-model="user.grado" label="Grado" required></v-text-field>
          </v-form>
        </v-col>
        
      </div>
    </div>
    <div class="col-md-5">
      <div class="col-md-12">
        <v-col cols="12" sm="12" md="80">
          <div class="overline text-center">ELIGE UNA IMAGEN</div>
          <hr />
          <v-file-input
            v-model="image"
            :rules="rules"
            accept="image/png, image/jpeg, image/bmp, image/jpg"
            placeholder="Elige una imagen"
            prepend-icon="mdi-camera"
            label="Avatar"
            id="photoInput"
            class="d-none"
            @change="updatePreview()"
          ></v-file-input>
          <v-card class="mx-auto">
            <img
              @click="openFI"
              id="img-preview"
              src="../../assets/img/no-image.png"
              alt="img-preview"
              class="img-prev"
            />
            <v-card-actions v-if="image.name" class="mx-auto mb-2">
                <v-spacer></v-spacer>
                <v-btn  color="red darken-1" text @click="removeImagen">QUITAR IMAGEN</v-btn>
            </v-card-actions>
          </v-card>
          
        </v-col>
      </div>
    </div>
    <v-card-actions class="ml-auto mr-4 mb-2">
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="back">Atras</v-btn>
        <v-btn color="blue darken-1" text @click="save">GUARDAR</v-btn>
    </v-card-actions>
  </div>
</template>

<script>
export default {
  data: () => ({
    user: {},
    image: {},
    rules: [
        value => !value || value.size < 2000000 || 'Avatar size should be less than 2 MB!',
    ],
    }),
  methods: {
    openFI() {
      const fileInput = document.getElementById("photoInput");
      photoInput.click();
    },
    updatePreview() {
        console.log(this.image);
      if(this.image) {
        const reader = new FileReader();
        reader.readAsDataURL(this.image);
        reader.onload = () => {
          const imgPrev = document.getElementById("img-preview");
          imgPrev.src = reader.result;
        };
      }
    },
    removeImagen() {
        document.getElementById("img-preview").src = 'https://es.zenit.org/wp-content/uploads/2018/05/no-image-icon.png';
        this.image = {}
    },
    back() {
        this.$emit('update:carousel', -1)
    },
    save() {
        this.$emit('update:dialog', false)
    },
  }
};
</script>

<style>
.details-alu {
  display: flex;
  flex-flow: row nowrap;
}
</style>