<template>
  <div class="row">
    <div class="col-sm-6 part1 mb-0 pb-0">
      <v-card-text>
        <v-container class="mb-0 pb-0">
          <div class="overline">DATOS DEL ALUMNO</div>
          <div class="row form-data">
            <div class="col-md-12">
              <v-form ref="form">
                <v-text-field
                  outlined
                  @keyup="consultarDNI"
                  v-model="user.dni"
                  label="DNI"
                  required
                ></v-text-field>
                <v-text-field outlined v-model="user.nombres" label="Nombre" required></v-text-field>
                <v-text-field
                  outlined
                  v-model="user.apellidoPaterno"
                  label="Apellido Paterno"
                  required
                ></v-text-field>
                <v-text-field
                  outlined
                  v-model="user.apellidoMaterno"
                  label="Apellido Materno"
                  required
                ></v-text-field>
                <v-text-field outlined v-model="user.direccion" label="Domicilio" required></v-text-field>
                <v-menu
                  ref="menu"
                  v-model="menu"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  min-width="290px"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      outlined
                      v-model="date"
                      label="Fecha de Nacimiento"
                      readonly
                      v-on="on"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    ref="picker"
                    v-model="date"
                    :max="new Date().toISOString().substr(0, 10)"
                    min="1950-01-01"
                    @change="saveDate"
                  ></v-date-picker>
                </v-menu>
              </v-form>
            </div>
          </div>
        </v-container>
      </v-card-text>
      <v-card-actions class="pt-0 mt-0">
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="close">Cerrar</v-btn>
        <v-btn color="blue darken-1" text @click="next">Siguiente</v-btn>
      </v-card-actions>
    </div>
    <div id="childs" class="col-sm-6 fondo-2 m-0 p-0">
      <v-carousel width="100%" height="658" :show-arrows="false" cycle>
        <v-carousel-item
          src="../../assets/img/ni単o.jpg"
          reverse-transition="fade-transition"
          transition="fade-transition"
        ></v-carousel-item>
        <v-carousel-item
          src="../../assets/img/ni単o2.jpg"
          reverse-transition="fade-transition"
          transition="fade-transition"
        ></v-carousel-item>
        <v-carousel-item
          src="../../assets/img/ni単o3.jpg"
          reverse-transition="fade-transition"
          transition="fade-transition"
        ></v-carousel-item>
      </v-carousel>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
    data: () => ({
        tokenReniec:
        "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJlbWFpbCI6Imx1aXNtZWxnYXJlam9hbGFyY29uQGdtYWlsLmNvbSJ9.sPuL_WIleq7us1RzH1feIjWG_7NC20Jj_9LBgvpQivU",
        user: {
            nombres: "",
            apellidoPaterno: "",
            apellidoMaterno: "",
            dni: "",
            direccion: ''
        },
        date: null,
        menu: false,
        /* items: [
        {
            src: "../assets/img/ni単o.jpg"
        },
        {
            src: "../assets/img/ni単o2.jpg"
        },
        {
            src: "../assets/img/ni単o3.jpg"
        }
        ] */
  }),
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  },
  methods: {
    close() {
      this.$emit("update:dialog", false);
    },
    next() {
      this.$emit("update:carousel", 1);
    },
    saveDate(date) {
      this.$refs.menu.save(date);
    },
    async consultarDNI() {
      try {
        if (this.user.dni.length >= 8) {
          const url = "http://dniruc.apisperu.com/api/v1/dni/";
          const res = await axios.get(
            url + this.user.dni + "?token=" + this.tokenReniec
          );
          if (res.data) {
            this.user.nombres = res.data.nombres;
            this.user.apellidoPaterno = res.data.apellidoPaterno;
            this.user.apellidoMaterno = res.data.apellidoMaterno;
          }
        }
      } catch (error) {}
    }
  }
};
</script>

<style>
.card-bg {
  background: #ad5389; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #3c1053,
    #ad5389
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #3c1053,
    #ad5389
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color: white;
}

.img-prev {
  width: 100%;
  height: auto;
  cursor: pointer;
  border-radius: 5px 5px 0px 0px;
}
.form-data,
.form-data2 {
  color: #fff;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}
.form-data2 {
  padding-top: 0;
  margin-top: 0;
}
.overline {
  color: #000;
}
.modal-content {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}
#childs {
  width: 100%;
  /* height: auto; */
  background-image: url(../../assets/img/ni単o2.jpg);
  background-size: cover;
  background-position: center;
  transition: ease-in 2s;
}

.fondo2 img {
  max-width: 400px;
  width: 100%;
}

.row {
  /* background: #1E1E1E; */
  background: #fff;
}

.v-card,
.v-sheet {
  border-radius: none;
  box-shadow: none;
}

@media screen and (max-width: 573px) {
  #childs {
    display: none;
  }
  .v-card__actions {
    margin-bottom: 50px;
  }
}
</style>