<template >
  <button class="btn ml-auto " @click="playButton" >
        <i v-if="iconAccept" class="material-icons">
            {{ addIcon }}
        </i>
        {{ name }}
  </button>
</template>

<script>
export default {
    props: {
        name: String,
        addClass: String,
        iconAccept: Boolean,
        addIcon: String,
        addColor: String
    },
    mounted() {
        const button = document.getElementsByClassName('btn')[0];
        const CLASS = ' '+this.$props.addClass;
        button.className += CLASS;
    },
    methods: {
        playButton() {
            this.$emit('click');
        } 
    }
}
</script>

<style>
.btn {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    align-items: center;
}

.btn-outline-info {
    color: #fff ;
    border: 1px solid #1976d2 ;
    background: #1976d2 ;
}
.btn-outline-info:hover {
    background: #4181c0 ;
}

.btn-outline-dark {
    color: #000;
    border: 1px solid #000;
}
.btn-outline-dark:hover {
    background: #000;
}

.btn-outline-morado {
    color: #fff;
    border: 1px solid #74569E;
    background: rgb(104, 77, 141);
}
.btn-outline-morado:hover {
    color: #fff;
    border: 1px solid #74569E;
    background: #74569E;
}
</style>